.pixel {
  animation: fade-in 0.5s linear forwards;
  content: "*";
  font-family: menlo;
  opacity: 0;
  pointer-events: none;
  position: fixed;
}

.pixel-button {
  align-items: center;
  background-color: var(--white);
  border: solid 1px var(--black);
  bottom: 1rem;
  color: var(--black);
  display: flex;
  font-family: menlo;
  height: 1.5rem;
  justify-content: center;
  position: fixed;
  right: 1rem;
  width: 1.5rem;

  &.active {
    background-color: var(--black);
    color: var(--white);
  }

  &:hover {
    background-color: var(--black);
    color: var(--white);
    cursor: pointer;
  }
}

@keyframes fade-in {
  to {
    opacity: 1;
  }
}
